import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import useWindowSize from 'react-use/lib/useWindowSize'
import queryString from 'query-string'

import Grid from '@material-ui/core/Grid'
import Particles from 'react-particles-js'

import { tagPageView, tagEvent } from '../Common/TagManager'

import GameTile from './GameTile'

import './GameLevel.css'

function GameLevel (props) {
    tagPageView('/app/level', 'choose level')
    const { width, height } = useWindowSize()
    const history = useHistory()
    const { category, lang } = queryString.parse(props.location.search)

    const handleGameTileClick = (value) => {
        //const category = props.location.state.category
        tagEvent('game', 'level', value)
        
        history.push({
            pathname: '/app/game',
            search: '?category=' + category + '&level=' + value + '&lang=' + lang,
            state: { category: category, level: value, lang: lang }
        })
    }

    useEffect(() => {
        localStorage.removeItem('wordsSolvedArray')
        localStorage.removeItem('currentWordTextFieldsSolved')
    });

    return (
        <div className='home'>
            <div className='game-body '>
                <div className='particles-container'>
                <Particles
                    width={width}
                    height={height}
                    style={{ width: width, height:height }}
                    params={{
                        "particles": {
                            "number": {
                                "value": 160,
                                "density": {
                                    "enable": false
                                }
                            },
                            "size": {
                                "value": 3,
                                "random": true,
                                "anim": {
                                    "speed": 4,
                                    "size_min": 0.3
                                }
                            },
                            "line_linked": {
                                "enable": false
                            },
                            "move": {
                                "random": true,
                                "speed": 1,
                                "direction": "top",
                                "out_mode": "out"
                            }
                        },
                        "interactivity": {
                            "events": {
                                "onhover": {
                                    "enable": true,
                                    "mode": "bubble"
                                },
                                "onclick": {
                                    "enable": true,
                                    "mode": "repulse"
                                }
                            },
                            "modes": {
                                "bubble": {
                                    "distance": 250,
                                    "duration": 2,
                                    "size": 0,
                                    "opacity": 0
                                },
                                "repulse": {
                                    "distance": 400,
                                    "duration": 4
                                }
                            }
                        }
                    }} />
                </div>
                <Grid style={{ marginTop:0, zIndex:200 }} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'first-letter', title:'אוֹת פּוֹתַחַת', imageurl: process.env.PUBLIC_URL + '/first-letter.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/first-letter.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'last-letter', title:'אוֹת סוֹגֶרֶת', imageurl: process.env.PUBLIC_URL + '/last-letter.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/last-letter.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'middle-letter', title:'אוֹת אֶמְצָעִית', imageurl: process.env.PUBLIC_URL + '/middle-letter.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/middle-letter.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'entire-word', title:'כָּל הַמִּלָּה', imageurl: process.env.PUBLIC_URL + '/entire-word.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/entire-word.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default GameLevel
