import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import useWindowSize from 'react-use/lib/useWindowSize'
import { tagPageView, tagEvent } from '../Common/TagManager'

import Grid from '@material-ui/core/Grid'
import Particles from 'react-particles-js'

import { gameData } from "./GameData"
import GameTile from './GameTile'

import './Home.css'

function Home () {
    const lang = 'he'
    const history = useHistory()
    const { width, height } = useWindowSize()
    const [categories, setCategories] = useState([])

    tagPageView('/app', 'choose category')

    
    const handleGameTileClick = (value) => {
        console.log('gameTileClick(home): ' + value)

        tagEvent('game', 'category', value)

        history.push({
            pathname: '/app/level',
            search: '?category=' + value + '&lang=' + lang,
            state: { category: value }
        })
    }

    useEffect(() => {
        localStorage.removeItem('wordsSolvedArray')
        localStorage.removeItem('currentWordTextFieldsSolved')

        const initCategoriesArray = () => {
            /*
            category: {
                title: {
                    he: {
                        title: "בַּבַּיִת"
                    },
                    en: {
                        title: "home"
                    }
                },
                value: "home"
        }
            */
            if (categories.length <= 0) {
                const tempCategories = new Array(gameData.length)

                for (var i=0; i < gameData.length; i++) {
                    const category = gameData[i].category
                    tempCategories[i] = category
                }
                setCategories(tempCategories)
            }
        }
        initCategoriesArray()
    },[categories]);
    
    return (
        <div className='home'>
            <div className='game-body'>
                <div className='particles-container'>
                <Particles
                    width={width}
                    height={height}
                    style={{ width: width, height:height }}
                    params={{
                        "particles": {
                            "number": {
                                "value": 160,
                                "density": {
                                    "enable": false
                                }
                            },
                            "size": {
                                "value": 3,
                                "random": true,
                                "anim": {
                                    "speed": 4,
                                    "size_min": 0.3
                                }
                            },
                            "line_linked": {
                                "enable": false
                            },
                            "move": {
                                "random": true,
                                "speed": 1,
                                "direction": "top",
                                "out_mode": "out"
                            }
                        },
                        "interactivity": {
                            "events": {
                                "onhover": {
                                    "enable": true,
                                    "mode": "bubble"
                                },
                                "onclick": {
                                    "enable": true,
                                    "mode": "repulse"
                                }
                            },
                            "modes": {
                                "bubble": {
                                    "distance": 250,
                                    "duration": 2,
                                    "size": 0,
                                    "opacity": 0
                                },
                                "repulse": {
                                    "distance": 400,
                                    "duration": 4
                                }
                            }
                        }
                    }} />
                </div>
            
                <Grid style={{marginTop:0, zIndex:200}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'fairy-tale', title:'אַגָּדוֹת', imageurl: process.env.PUBLIC_URL + '/images/fairy-tale/frog.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/fairy-tale/fairy-tale_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'love', title:'אַהֲבָה', imageurl: process.env.PUBLIC_URL + '/images/love/heart.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/love/love_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'food', title:'אֹכֶל', imageurl: process.env.PUBLIC_URL + '/images/food/toast.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/food/food_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'home', title:'בַּבַּיִת', imageurl: process.env.PUBLIC_URL + '/images/home/image.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/home/home_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
                <Grid style={{marginTop:12, zIndex:200}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'animals', title:'בַּעֲלֵי חַיִּים', imageurl: process.env.PUBLIC_URL + '/images/animals/giraffe.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/animals/animals_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'fruits-and-vegetables', title:'יְרָקוֹת וּפֵרוֹת', imageurl: process.env.PUBLIC_URL + '/images/fruits-and-vegetables/027-strawberry.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/fruits-and-vegetables/fruits-and-vegetables_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'tools', title:'כֵּלִים', imageurl: process.env.PUBLIC_URL +'/images/tools/hammer.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/tools/tools_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'science', title:'מַדָּע וְחָלָל', imageurl: process.env.PUBLIC_URL + '/images/science/planet-earth.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/science/science_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
                <Grid style={{marginTop:12, zIndex:200}} container direction='row' justify='center' alignItems='center' spacing={3}>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'kitchen', title:'מִטְבָּח', imageurl: process.env.PUBLIC_URL + '/images/kitchen/oven-mitts.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/kitchen/kitchen_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'devices', title:'מַכְשִׁירִים', imageurl: process.env.PUBLIC_URL + '/images/devices/desk-lamp.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/devices/devices_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'medicine', title:'רְפוּאָה', imageurl: process.env.PUBLIC_URL + '/images/medicine/hospital.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/medicine/medicine_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                    <Grid item xs={6} sm={3}>
                        <GameTile gameTile={{ value:'transport', title:'תַּחְבּוּרָה', imageurl: process.env.PUBLIC_URL + '/images/transport/aeroplane.svg', soundUrl: process.env.PUBLIC_URL + '/sounds/' + lang + '/transport/transport_category.m4a' }} onGameTileClick={handleGameTileClick}></GameTile>
                    </Grid>
                </Grid>
            </div>
        </div>
    )
}

export default Home
